<template>
	<!--pages/orderList/orderList.wxml-->

	<view class="container">

		<view class="main">
			<view class="empty" v-if="list.length==0">
				还没有任何相关订单
			</view>
			<!-- 订单列表 -->
			<block v-for="(item, index) in list" :key="index">
				<view class="prod-item">
					<view class="order-num">
						<!--          <text>订单编号：{{item.orderNumber}}</text -->>
						<view class="order-state">
							<text
								class="order-sts   red">{{item.aftersaleType==0?'仅退款': item.aftersaleType==1? '退货退款' : '换货'}}</text>

						</view>
					</view>

					<!-- 商品列表 -->
					<!-- 一个订单单个商品的显示 -->
					<view>
						<view class="item-cont" @tap="toAftersaleDetailPage"
							:data-aftersaleOrderId="item.aftersaleOrderId">
							<view class="prod-pic">
								<image :src="item.orderItem.pic"></image>
							</view>
							<view class="prod-info">
								<view class="prodname">
									{{item.orderItem.prodName}}
								</view>
								<view class="prod-info-cont">{{item.orderItem.skuName}}</view>
								<view class="price-nums">
									<text class="prodprice"><text class="symbol">退款：￥</text>
										<text class="big-num">{{ item.refundAmount }}</text>
									</text>
								</view>
							</view>
						</view>
					</view>

					<!-- end 商品列表 -->
					<view class="prod-foot">
						<view class="btn">
							<text v-if="item.phase===4 || item.phase===5" class="button" @tap="onDeleteOrder"
								:data-aftersaleOrderId="item.aftersaleOrderId" hover-class="none">删除记录</text>
							<!-- <text class="button warn" @tap :data-ordernum="item.orderNumber" hover-class="none">再次购买</text> -->
							
							<text class="button warn" @tap="toAftersaleDetailPage"
								:data-aftersaleOrderId="item.aftersaleOrderId" hover-class="none">查看详情</text>
						</view>
					</view>
				</view>
			</block>


		</view>
	</view>
	<!-- end 订单列表 -->
</template>

<script module="wxs" lang="wxs" src="../../wxs/number.wxs"></script>

<script>
	var http = require("../../utils/http.js");
	var config = require("../../utils/config.js");

	export default {
		data() {
			return {
				list: [],
				current: 1,
				pages: 0,
			};
		},

		components: {},
		props: {},

		/**
		 * 生命周期函数--监听页面加载
		 */
		onLoad: function(options) {
			this.loadOrderData(1);
			
		},

		/**
		 * 生命周期函数--监听页面初次渲染完成
		 */
		onReady: function() {},

		/**
		 * 生命周期函数--监听页面显示
		 */
		onShow: function() {},

		/**
		 * 生命周期函数--监听页面隐藏
		 */
		onHide: function() {},

		/**
		 * 生命周期函数--监听页面卸载
		 */
		onUnload: function() {},

		/**
		 * 页面相关事件处理函数--监听用户下拉动作
		 */
		onPullDownRefresh: function() {},

		/**
		 * 页面上拉触底事件的处理函数
		 */
		onReachBottom: function() {
			if (this.current < this.pages) {
				this.loadOrderData(this.current + 1);
			}
		},

		/**
		 * 用户点击右上角分享
		 */
		onShareAppMessage: function() {},
		methods: {
			/**
			 * 加载订单数据
			 */
			loadOrderData: function(current) {
				var ths = this;
				uni.showLoading(); //加载订单列表

				var params = {
					url: "/p/aftersale/order/page",
					method: "GET",
					data: {
						current: current,
						size: 10,
					},
					callBack: function(res) {
						//console.log(res);
						var list = [];

						if (res.current == 1) {
							list = res.records;
						} else {
							list = ths.list;
							Array.prototype.push.apply(list, res.records);
						}

						ths.setData({
							list: list,
							pages: res.pages,
							current: res.current
						});
						uni.hideLoading();
					}
				};
				http.request(params);
			},

	

			/**
			 * 删除记录
			 */
			onDeleteOrder: function(e) {
				var aftersaleOrderId = e.currentTarget.dataset.aftersaleOrderId;
				var ths = this;
				uni.showModal({
					title: '',
					content: '是否要删除此记录？',
					confirmColor: "#3e62ad",
					cancelColor: "#3e62ad",
					cancelText: '否',
					confirmText: '是',

					success(res) {
						if (res.confirm) {
							uni.showLoading({
								mask: true
							});
							var params = {
								url: "/p/aftersale/order/delete" + aftersaleOrderId,
								method: "PUT",
								data: {},
								callBack: function(res) {
									//console.log(res);
									ths.loadOrderData(1);
									uni.hideLoading();
								}
							};
							http.request(params);
						} else if (res.cancel) { //console.log('用户点击取消')
						}
					}

				});
			},


			/**
			 * 查看订单详情
			 */
			toAftersaleDetailPage: function(e) {
				uni.navigateTo({
					url: '/pages/aftersale-detail/aftersale-detail?aftersaleOrderId=' + e.currentTarget.dataset.aftersaleOrderId
				});
			},
		}
	};
</script>
<style>
	@import "./aftersale-list.css";
</style>